import React, { Component } from "react";
import { Input, Col } from "reactstrap";
import "./Search.css";
import axios from "axios";
import loading from "../../../asset/loadingNes.gif";

import TopComponent from "./TopComponent/TopComponent";
import SongComponent from "./SongComponent/SongComponent";
import AlbumSearchComponent from "./AlbumComponent/AlbumSearchComponent";



export class SearchComponent extends Component {
  constructor(props) {
    super(props);


    this.state = {
      result: [],
      getSong: null,
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
        Authorization:
          `Bearer ${this.props.token}`
      },
      status: false,
      typing: false,
      data: [],
      tokenExpired: false
    };
  }

  searchHandler = e => {
    if (e.target.value === "") {
      this.setState({
        typing: false,
        status: false
      });
    } else {
      this.setState({
        typing: true
      });
    }
    var textToSearch = e.target.value.replace(/ /g, "+");
    clearTimeout(this.state.getSong);
    this.state.getSong = setTimeout(() => {
      axios
        .get(
          `https://api.spotify.com/v1/search?q=${textToSearch}&type=track,album&limit=20`,
          { headers: this.state.headers }
        )
        .then(res => {
          this.setState({
            status: true,
            data: res.data
          });
        })
        .catch(error => {
          this.setState({
            tokenExpired: true
          });
          this.props.history.push("/error")
        });
    }, 1000);
  };

  render() {
    let layout = (
      <div className="content">
        <TopComponent data={this.state.data} />

        <SongComponent data={this.state.data.tracks} />

        <AlbumSearchComponent data={this.state.data.albums} />
      </div>
    );
    return !this.state.tokenExpired ? (
      <Col sm="10" id="searchBox">
        <div className="search">
          <Input
            id="SearchInput"
            autoFocus
            placeholder="Start typing..."
            onChange={this.searchHandler}
          />
        </div>

        {this.state.typing ? (
          this.state.status ? (
            this.state.data.length > 0 ? (
              <div>No any data</div>
            ) : (
              <>{layout}</>
            )
          ) : (
            <div className="loadingSearch">
              <img src={loading} alt="loading" />
            </div>
          )
        ) : (
          <div className="result">
            <h4>Search Spotify</h4>
            <p>Find your favorite songs,album, podcasts and playlists.</p>
          </div>
        )}
      </Col>
    ) : (
      window.location = "https://spotifytoken01.herokuapp.com/"
    )
    // (
    //   <div></div>
      // <BrowserRouter>
      //   <Route path="/error" component={ErrorComponent} />
      // </BrowserRouter>
    // );
  }
}

export default SearchComponent;
