import React, { Component } from "react";
import OneAlbum from "./OneAlbum";
import { Col, Row } from "reactstrap";
import axios from "axios";
import "./Library.css"

export class LibraryComponent extends Component {

  click = index =>{
    axios
      .get(
        `https://hailduong.github.io/lyduong/react-assignment/spotify-2/apis/album/${index}.json`
      )
      .then(response => {
        this.props.updateAlbum(response.data,index);
        this.props.urlData.history.push("/albumdetails");
      })
      .catch(error => {
        console.log("Error", error);
      });
  }

  render() {
    const { all_data, status } = this.props.all_data;
    return (
      <>
          {status ? (
          <Col sm="10" className="libContent">
            <div className="category">Album</div>

            <Row>
              {all_data.all_album.map((album, index) => (
                <OneAlbum
                  key={index}
                  albumDetails={this.props.album}
                  album={album}
                  index={index}
                  click={this.click}
                />
              ))}
            </Row>
          </Col>
        ) : (
          <div>Loading...</div>
        )}
      
      </>
    );
  }
}

export default LibraryComponent;
