import axios from "axios";
import React from "react";
import SpotifyWebApi from "spotify-web-api-js";

const get_data = ChildComponent => {
  const spotifyApi = new SpotifyWebApi();
  class WrappedComponent extends React.Component {
    constructor(props) {
      super(props);

      const params = this.getHashParams();
      //get access token
      const token = params.access_token;
      spotifyApi.setAccessToken(token);
      
      this.state = {
        all_data: {},
        status: false,
        album: [],
        playing: false,
        stopping: true,
        recentPlay: [],
        repeat: false,
        TOKEN : token || ""
      };
    }

    getHashParams() {
      let hashParams = {};
      let e,
        r = /([^&;=]+)=?([^&;]*)/g,
        q = window.location.hash.substring(1);
      e = r.exec(q);
      while (e) {
        hashParams[e[1]] = decodeURIComponent(e[2]);
        e = r.exec(q);
      }
      return hashParams;
    }

    componentDidMount() {
      const URL_USER =
        "https://raw.githubusercontent.com/hailduong/lyduong/master/react-assignment/spotify/apis/userInfo.json";
      const URL_ALLALBUM =
        "https://hailduong.github.io/lyduong/react-assignment/spotify-2/apis/yourLibrary.json";

      const URL_FIRST_ALBUM =
        "https://hailduong.github.io/lyduong/react-assignment/spotify-2/apis/album/1.json";

      let user = axios
        .get(URL_USER)
        .then(res => res.data)
        .catch(error => console.log("Error User", error));
      let all_album = axios
        .get(URL_ALLALBUM)
        .then(res => res.data)
        .catch(error => console.log("Error All Album", error));

      let first_album = axios
        .get(URL_FIRST_ALBUM)
        .then(res => res.data)
        .catch(error => console.log("Error First Album", error));

      axios
        .all([user, all_album, first_album])
        .then(res => {
          this.setState({
            all_data: {
              user: res[0],
              all_album: res[1]
            },
            album: res[2],
            status: true
          });
        })
        .catch(error => {
          console.log("Promise All Error", error);
        });
    }

    updateAlbum = (album, id) => {
      this.state.recentPlay.unshift({ id: id, album: album });

      this.setState(
        {
          ...this.state,
          album: album,
          recentPlay: [...this.state.recentPlay],
          playing: true,
          stopping: false
        },

        () => {
          if (this.state.recentPlay.length > 4) {
            this.state.recentPlay.splice(-1, 1);
          }
          this.setState({
            all_data: {
              ...this.state.all_data,
              user: {
                ...this.state.all_data.user,
                playing: {
                  album: this.state.album.name,
                  track: "1"
                }
              }
            }
          });
        }
      );
    };

    control_play_pause = () => {
      this.setState(prevState => ({
        ...this.state,
        playing: !prevState.playing,
        stopping: !prevState.stopping
      }));
    };

    control_play = () => {
      this.setState({
        ...this.state,
        playing: true,
        stopping: false
      });
    };

    control_pause = () => {
      this.setState({
        ...this.state,
        playing: false
      });
    };

    control_stop = () => {
      this.setState(prevState => ({
        ...this.state,
        all_data: {
          ...this.state.all_data,
          user: {
            ...this.state.all_data.user,
            playing: {
              album: this.state.album.name,
              track: "1"
            }
          }
        },
        playing: false,
        stopping: true
      }));
    };

    control_start = () => {
      this.setState(prevState => ({
        ...this.state,
        all_data: {
          ...this.state.all_data,
          user: {
            ...this.state.all_data.user,
            playing: {
              album: this.state.album.name,
              track: "1"
            }
          }
        },
        playing: true,
        stopping: false
      }));
    };

    choose_song = track => {
      this.setState({
        ...this.state,
        all_data: {
          ...this.state.all_data,
          user: {
            ...this.state.all_data.user,
            playing: track
          }
        },
        playing: true,
        stopping: false
      });
    };

    control_repeat = () => {
      this.setState(prevState => ({
        ...this.state,
        repeat: !prevState.repeat
      }));
      console.log(this.state);
    };

    render() {
      return (
        <ChildComponent
          all_data={this.state}
          updateAlbum={this.updateAlbum}
          control_play={this.control_play}
          control_pause={this.control_pause}
          control_stop={this.control_stop}
          control_start={this.control_start}
          choose_song={this.choose_song}
          control_repeat={this.control_repeat}
          recentPlay={this.state.recentPlay}
          token={this.state.TOKEN}
        />
      );
    }
  }
  return WrappedComponent;
};

export default get_data;
