import React, { Component } from "react";
import "./ControlComponent.css";

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faCheck,
  faRandom,
  faStepBackward,
  faPlay,
  faStepForward,
  faUndo,
  faList,
  faTv,
  faVolumeDown,
  faPause,
  faBan
} from "@fortawesome/free-solid-svg-icons";
import { Progress, Row, Col } from "reactstrap";
import { setInterval, clearInterval } from "timers";

class ControlComponent extends Component {
  constructor(props) {
    super(props);
    this.state = {
      interval: null,
      startSeconds: 0,
      startMinute: 0,
      totalTime: "",
      percent: 0,
      endTime: 0
    };
  }

  componentDidMount(){
    if(this.props.all_data.playing === true){
      this.startCount();
    } else {
      clearInterval(this.state.interval);
    }
  }

  componentWillReceiveProps(nextProps){
    if(nextProps.all_data.playing === true){
      this.startCount();
    } else {
      clearInterval(this.state.interval);
    }

    if(nextProps.all_data.all_data.user.playing !== this.props.all_data.all_data.user.playing){
      this.setState({
        startSeconds : 0,
        startMinute : 0,
        percent : 0
      })
    }

    if(nextProps.all_data.stopping !== this.props.all_data.stopping){
      this.setState({
        startSeconds : 0,
        startMinute : 0,
        percent : 0
      })

      if(nextProps.all_data.stopping === true){
        clearInterval(this.state.interval)  
      } else {
        this.startCount();
      }
    }

  }

  componentDidUpdate(prevProps, prevState) {
    try {
      if (
        prevProps.all_data.all_data.user.playing.track !==
        this.props.all_data.all_data.user.playing.track
      ) {
        this.setState({
          startSeconds: 0,
          startMinute: 0,
          percent: 0
        });

        this.startCount();
      }
    } catch (error) {}
  }

  playingStatus() {
    if (this.props.playing) {
      this.startCount();
    } else {
      clearInterval(this.state.interval);
    }
  }

  millisecToMinutes = millisec => {
    var minutes = Math.floor(millisec / 60000);
    var seconds = ((millisec % 60000) / 1000).toFixed(0);
    return minutes + ":" + (seconds < 10 ? "0" : "") + seconds;
  };

  startCount = () => {
    clearInterval(this.state.interval);
    this.state.interval = setInterval(() => {
      var totalLength = parseInt(
        this.props.all_data.album.tracks[
          parseInt(this.props.all_data.all_data.user.playing.track - 1)
        ].legnth
      );
      var currentLength =
        this.state.startMinute * 60000 + this.state.startSeconds;
      this.setState(
        prevState => ({
          startSeconds: prevState.startSeconds + 1000,
          percent: (currentLength / totalLength) * 100
        }),
        () => {
          if (this.state.startSeconds === 60000) {
            this.setState(prevState => ({
              startMinute: prevState.startMinute + 1,
              startSeconds: 0
            }));
          }

          if (currentLength >= totalLength) {
            this.props.repeat ? this.control("REPEAT") : this.control("NEXT");
          }
        }
      );
    }, 1000);
  };


  control = CONTROL => {
    const { user } = this.props.all_data.all_data;
    const { album } = this.props.all_data;
    // const { album, user } = this.props;
    var chooseTrack = user.playing.track;
    switch (CONTROL) {
      case "RANDOM": {
        chooseTrack =
          album.tracks[Math.floor(Math.random() * (album.tracks.length + 1))]
            .number;
        this.startCount();
        break;
      }
      case "PREVIOUS": {
        console.log(album.tracks.length);
        if (parseInt(user.playing.track) === 1) {
          chooseTrack = album.tracks.length;
        } else {
          chooseTrack =
            album.tracks[parseInt(user.playing.track - 1) - 1].number;
        }
        this.startCount();
        break;
      }
      case "PLAY": {
        this.resume();
        break;
      }
      case "PAUSE": { 
        this.pause_music();
        break;
      }
      case "NEXT": {
        this.setState({
          startSeconds: 0,
          startMinute: 0,
          percent: 0
        });
        if (parseInt(user.playing.track) >= album.tracks.length) {
          chooseTrack = "1";
        } else {
          chooseTrack =
            album.tracks[parseInt(user.playing.track - 1) + 1].number;
        }
        this.startCount();
        break;
      }
      case "REPEAT": {
        this.setState({
          startSeconds: 6,
          startMinute: 20000,
          percent: 0
        });
        chooseTrack = album.tracks[parseInt(user.playing.track - 1)].number;
        this.startCount();
        break;
      }
      default: {
        console.log("Haha Th Ngu");
      }
    }
    console.log(chooseTrack);
    let selectedSong = {
      album: this.props.all_data.album.name,
      track: chooseTrack+""
    };
    this.props.choose_song(selectedSong);
  };

  random = () => {
    this.control("RANDOM");
  };

  previous = () => {
    this.control("PREVIOUS");
  };

  play = () => {
    if (this.props.all_data.playing) {
      this.props.control_pause();
      this.startCount();
    } else {
      this.props.control_play();
      clearInterval(this.state.interval);
    }
  };

  next = () => {
    this.control("NEXT");
  };

  repeat = () => {
    this.props.control_repeat();
  };

  render() {
    const { all_data } = this.props;
    const { user } = this.props.all_data.all_data;
    console.log(this.props);
    return (
      <>
        {all_data.status ? (
          <Row>
            <div className="controlComponent">
              <Col xs="2" sm="1">
                <div className="imageControl">
                  <img src={all_data.album.covers.small} alt="Loading" />
                </div>
              </Col>

              <Col xs="2" sm="2">
                {all_data.album.tracks.map((track, index) =>
                  user.playing.track === track.number ? (
                    <div className="detailsControl" key={index}>
                      {track.title}
                      <div className="singer">{all_data.album.artist}</div>
                    </div>
                  ) : null
                )}
              </Col>
              <Col xs="2" sm="1">
                <div className="checkButton">
                  <FontAwesomeIcon icon={faCheck} />
                </div>
              </Col>
              <Col xs="2" sm="4">
                <div className="flowControl">
                  <FontAwesomeIcon icon={faRandom} onClick={this.random} />
                  <FontAwesomeIcon
                    icon={faStepBackward}
                    onClick={this.previous}
                  />
                  <FontAwesomeIcon
                    icon={this.props.all_data.playing ? faPause : faPlay}
                    onClick={this.play}
                    id="playButton"
                  />
                  <FontAwesomeIcon icon={faStepForward} onClick={this.next} />
                  <FontAwesomeIcon
                    icon={this.props.all_data.repeat ? faBan : faUndo}
                    onClick={this.repeat}
                  />
                </div>
                <div className="progressFlow">
                  <div className="startTime">
                    {`${this.state.startMinute} :${
                      this.state.startSeconds / 1000 < 10
                        ? "0" + this.state.startSeconds / 1000
                        : this.state.startSeconds / 1000
                    }`}
                  </div>
                  <div className="progressBar">
                    <Progress value={this.state.percent} />
                  </div>
                  <div className="endTime">
                    {this.millisecToMinutes(
                      this.props.all_data.album.tracks[
                        parseInt(
                          this.props.all_data.all_data.user.playing.track
                        ) - 1
                      ].legnth
                    )}
                  </div>
                </div>
              </Col>
              <Col xs="2" sm="3">
                <div className="musicControl">
                  <FontAwesomeIcon icon={faList} />
                  <FontAwesomeIcon icon={faTv} />
                  <FontAwesomeIcon icon={faVolumeDown} />
                  <Progress id="musicControlProg" value="0" />
                </div>
              </Col>
            </div>
          </Row>
        ) : null}
      </>
    );
  }
}

export default ControlComponent;
