import React, { Component } from "react";
import "./PlaylistComponentStyle.css";
import OneSongComponent from "./OneSongComponent";
import CopyRight from "./CopyRight";

class PlaylistComponent extends Component {
  constructor(props) {
    super(props);

    this.state = {
      activeIndex: 0
    };
  }

  millisecToMinutes = millisec => {
    var minutes = Math.floor(millisec / 60000);
    var seconds = ((millisec % 60000) / 1000).toFixed(0);
    return minutes + ":" + (seconds < 10 ? "0" : "") + seconds;
  };

  chooseSong = (track,index)=>{
    let selectedTrack = {
      album : this.props.all_data.album.name,
      track : track.number
    }
    this.props.choose_song(selectedTrack)
  }

  render() {
    const { all_data } = this.props;
  
    return (
      <>
        {
          all_data.status ? 
          <div className="playlistComponent">
            <div className="listSong">
              {all_data.album.tracks.map((track, index) => {
                return (
                  <div
                    key={track.number}
                    onClick={()=>this.chooseSong(track,index)}
                  >
                    <OneSongComponent
                      active={
                        parseInt(all_data.all_data.user.playing.track) - 1 === index
                          ? "active"
                          : ""
                      }
                      
                      track={track}
                      time={this.millisecToMinutes(parseInt(track.legnth))}
                    />
                  </div>
                );
              })}
            </div>
            <div className="copyRight">
              <CopyRight />
            </div>
          </div> : null
        }
          
      </>
    );
  }
}

export default PlaylistComponent