import React, { Component } from "react";
import "./App.css";
import loading from "./asset/loading.gif";

import { BrowserRouter, Route, Switch } from "react-router-dom";
import AlbumComponent from "./component/RightComponent/AlbumComponent/AlbumComponent";
import LibraryComponent from "./component/RightComponent/LibraryComponent/LibraryComponent";

import get_data from "./Data/GetData";

//Font Awesome
import "./component/FontAwesome";

//Component - Bootstrap
import { Col, Row } from "reactstrap";
import SideMenuComponent from "./component/Menu/SideMenuComponent";
import ControlComponent from "./component/ControlComponent/ControlComponent";

import { setTimeout, clearTimeout } from "timers";

import HomeComponent from "./component/RightComponent/HomeComponent/HomeComponent";
import SearchComponent from "./component/RightComponent/SearchComponent/SearchComponent";



class App extends Component {
  constructor(props) {
    super(props);

    this.state = {
      timeOut: null
    };
  }

  componentDidMount() {
    if (
      this.props.userStatus === false ||
      this.props.allAlbumStatus === false
    ) {
      this.state.timeOut = setTimeout(() => {
        alert("Connection Error, Try Again");
        window.location.reload();
      }, 5000);
    } else {
      clearTimeout(this.state.timeOut);
    }
  }

  componentDidUpdate() {
    if (this.props.userStatus === true && this.props.allAlbumStatus === true) {
      clearTimeout(this.state.timeOut);
    }
  }

  render() {
    const { status } = this.props.all_data;
    return (
      <>
        {status ? (
          <>
            <Row>
              <BrowserRouter>
                <>
                  <Col xs="6" sm="2">
                    <SideMenuComponent {...this.props} />
                  </Col>

                  <Switch>
                    <Route exact path="/nhothspotify" component={HomeComponent} />
                    <Route
                      path="/nhothspotify/library"
                      render={props => (
                        <LibraryComponent {...this.props} urlData={props} />
                      )}
                    />
                    <Route
                      path="/nhothspotify/search"
                      render={props => (
                        <SearchComponent {...this.props} />
                      )}
                    />
                    <Route
                      path="/nhothspotify/albumdetails"
                      render={() => <AlbumComponent {...this.props} />}
                    />
                  </Switch>
                </>
              </BrowserRouter>
            </Row>
            {
              this.props.all_data.playing && <ControlComponent {...this.props}/>
            }
            
          </>
        ) : (
          <div className="loading">
            <img src={loading} width="20%" alt="Waitting for Data" />
          </div>
        )}
      </>
    );
  }
}

export default get_data(App);
